<template>
  <div class="LayoutAside-root">
    <div class="logo">
      <img src="@/assets/logo.png" class="logo-img">

      <span v-show="showLogo" class="logo-text">流程编排DAG</span>
    </div>

    <MenuList :is-collapse="isCollapse" />

    <div class="collapse-box" @click="isCollapse = !isCollapse">
      <i :class="isCollapse ? 'el-icon-d-arrow-right' : 'el-icon-d-arrow-left'" />
    </div>
  </div>
</template>

<script>
import MenuList from './MenuList.vue';

export default {
  name: 'LayoutAside',
  components: {
    MenuList,
  },
  props: {},
  data() {
    return {
      isCollapse: false,
      showLogo: true,
    };
  },
  computed: {
    routes() {
      return this.$router.options.routes;
    },
  },
  watch: {
    isCollapse(newVal) {
      if (!newVal) {
        setTimeout(() => {
          this.showLogo = !newVal;
        }, 300);
      } else {
        this.showLogo = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.LayoutAside-root {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #001937;
  box-shadow: 4px 0px 16px 2px rgba(0, 25, 55, 0.3);
  border-radius: 0px 20px 20px 0px;
  overflow: visible;
}

.logo {
  display: flex;
  align-items: center;
  padding: 0 12px;
  margin-top: 13px;
  margin-bottom: 37px;
  color: #fff;
  overflow: hidden;
}
.logo-img {
  width: 40px;
  height: 40px;
}
.logo-text {
  flex-shrink: 0;
  margin-left: 8px;
}

.collapse-box {
  position: absolute;
  top: 48px;
  right: -16px;
  width: 32px;
  height: 32px;
  background-color: #F5F5F5;
  box-shadow: 0px 7px 12px 1px rgba(153, 158, 157, 0.36);
  border-radius: 50%;
  text-align: center;
  line-height: 32px;
}

</style>
