import Vue from 'vue';
import ElementUI from 'element-ui-lichi';
import 'element-ui-lichi/lib/theme-chalk/index.css';
import * as YLForm from '@thales/form';
import * as YLTable from '@thales/table';
import Editor from 'bin-ace-editor';
import App from './App.vue';
import router from './router';
import store from './store';
import '@thales/theme/src/index.css';

import './assets/scss/reset.scss';
import './assets/scss/public.scss';
import '@/directives/directives';

// 注册yl-form
Vue.use(YLForm);
// 注册yl-table
Vue.use(YLTable);
Vue.use(ElementUI);
Vue.config.productionTip = false;

// json-editor 按需引入需要的语言包皮肤等资源  https://wangbin3162.gitee.io/bin-ace-editor/#/guide
require('brace/ext/emmet'); // 如果是lang=html时需引入
require('brace/ext/language_tools'); // language extension
require('brace/mode/json');
require('brace/snippets/json');
require('brace/theme/chrome');

Vue.component(Editor.name, Editor);

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
